import { createRouter, createWebHashHistory } from 'vue-router';
import store from '@/script/store';
import Login from '@/pages/Login';
import Join from '@/pages/Join';
import UserSetting from '@/pages/member/UserSetting';
import AdminSetting from '@/pages/member/AdminSetting';
import Home from '@/components/Home';
import Introducing from '@/pages/homeTab/Introducing';
import Comment from '@/pages/comment/Comment';
import Board from '@/pages/homeTab/BoardList';
import BoardWrite from '@/pages/board/BoardWrite';
import BoardDetail from '@/pages/board/BoardDetail';
//import addIntroduction from '@/pages/homeTab/add_introduction';
import Test from '@/pages/board/test';
import SignOut from '@/pages/member/SignOut'

const requireAuth = () => (from, to, next) => {
    if (store.state.account.isLogin === true) {
      return next()
    }
    next('/login') 
}
// const beforeRouteUpdate = () => (to, from, next) => {
//     //        .
//     console.log(' :', from)
//     console.log(' :', to)
    
//     //     .
//     //          .
    
//     next() //  next()     .
//   }
const routes = [
    { path: '/', name : 'Home', component: Home },                                          // 
    { path: '/login', name : 'Login', component: Login },                                   // 
    { path: '/join', name : 'Join', component: Join },                                      // 
    { path: '/UserSetting', name : 'UserSetting', component: UserSetting },                 // 
    { path: '/AdminSetting', name : 'AdminSetting', component: AdminSetting },              // 
    { path: '/signOut', name : 'SignOut', component: SignOut },                             // 
    { path: '/comment', name : 'Comment', component: Comment },                             // 
    { path: '/introduce', name : 'Introducing', component: Introducing },                   // 
    
    { path: '/board', name : 'Board', component: Board },                                   //  
    { path: '/board/write', name : 'BoardWrite', component: BoardWrite},                    // 
    { path: '/board/detail/', name : 'BoardDetail', component: BoardDetail},                 //  
    { path: '/board/test', name : 'Test', component: Test, beforeEnter: requireAuth()}      // tes
];

const router = createRouter({
    mode: 'history',
    history: createWebHashHistory(),
    routes
});


