<template>
    <div>
        <textarea placeholder=" " class="comment-write-input"
                  style="height: 70px;" v-model="commentContent">
        </textarea>
        <div class="buttons-wrapper">
          <button class="btn btn-comment-write" @click="writeComment"> </button>
        </div>
    </div>
</template>

<script>

export default ({
    props:{
        parentBoardNo       : Object,
        parentUserNo        : Object,
        parentCommentNo     : Object
    },
    data (){
        return{
            commentUserId: localStorage.getItem('user_id'),
            commentContent: '' 
        }
    },
    methods:{
        writeComment() {  //   
            if (!this.commentContent.trim()) {
                alert('  .');
                return;
            }

            if(!this.$store.state.account.isLogin)      //  isLogin = true
            {
                alert('  .');
                this.$router.push({
                    name: 'Login'
                })
            }
            
            const apiUrl = this.$commentUrl + '/user/addComment/' + this.parentBoardNo
            const form = {
                "boardNo"          : this.parentBoardNo,
                "userNo"           : this.parentUserNo,
                "commentContent"   : this.commentContent,
                "parentCommentNo"  : this.parentCommentNo
            };
            
            console.log(' :', form);
            this.$axios.post(apiUrl, form)
            .then(() => {
                alert(' .')
                // form    
                this.$emit('comment-submitted', form);
    
                }).catch((err) => {
                    if (err.message.indexOf('Network Error') > -1) {
                        alert('  .\n   .')
                    }else if(err.response && err.response.status === 500){
                        alert('error');
                    }
                })

                this.commentContent = '';       //  
            },
    }
})
</script>

<style scoped>
    .comment-write-input, 
    .re-comment-input {
        resize: none;
        width: 100%;
        box-sizing: border-box;
        margin-bottom: 10px;
        padding: 8px;
        border: 1px solid #ccc;
        border-radius: 4px;
    }
    .buttons-wrapper {
        display: flex;
        justify-content: flex-end;
    }
    .btn-comment-write{
        padding: 8px 12px;
        border: 1px solid #007BFF;
        border-radius: 4px;
        background-color: #007BFF;
        color: #fff;
        cursor: pointer;
        transition: background-color 0.3s;
    }
</style>
