export default {
    // html img  
    imginHtmlChk(html) {
      const parser = new DOMParser();
      const doc = parser.parseFromString(html, 'text/html');
      const imgTags = doc.querySelectorAll('img');
      let imgSources = [];
  
      if (imgTags.length !== 0) {
        imgTags.forEach(img => {
          imgSources.push(img.src);
        });
        //console.log('imgSources : ' + JSON.stringify(imgSources));
  
        return imgSources;
      } else {
        return false;
      }
    },


    //  URL  HTML   src   
    updateBase64ToServerUrl(html, imgSources, serverImgUrls) {
      const parser = new DOMParser();
      const doc = parser.parseFromString(html, 'text/html');
      const imgTags = doc.querySelectorAll('img');

      imgTags.forEach((img, index) => {
          const base64Src = imgSources[index]; //  img  base64 
          const serverUrl = serverImgUrls[index]; //    URL
          img.src = img.src.replace(base64Src, serverUrl); // base64   URL 
      });

      return doc.documentElement.outerHTML; //  HTML 
    },

    // base64 
    extractBase64FromImgSrc(imgSrc) {
      const base64String = imgSrc.split(',')[1];
      return base64String;
    },

  }