<template>
  <div class="board-container">
    <!-- <form @submit.prevent="submitForm"> -->
      <textarea v-model="boardTitle" placeholder=" " class="board-title"></textarea>
      <div class="seperator"></div>

      <div class="tag-container">
        <TagInput :tags="tagsFromChild" ref="tagInputRef" @update:tags="updateTag"  />
      </div>    

      <div class="editor-container">
        <div class="editor-page">
            <!-- //<editor v-model="content"></editor> -->
            <!-- <Editor :content="contentFromChild" @update:content="onEditorChange" /> -->
            <Editor :content="contentFromChild" 
                    :parentFile="filesFromChild"
                    :parentBoardNo="this.boardNo" 
                    @update:content="onEditorChange"
                    @update:file="onImageAdded"/>
        </div>
      </div>

      <div class="board-btn">
        <button type="button" v-on:click="fnList" class="btn btn-board-cancle"></button>
        <button type="button" v-on:click="fnSave" class="btn btn-board-save"></button>
      </div>
    <!-- </form> -->
  </div>
</template>

<script>
import Editor from '@/pages/board/Editor.vue';
import TagInput from "@/pages/tag/TagInput.vue";
import base64 from '@/script/util/base64.js';
export default {
  components: { Editor, TagInput },
  data() {
    return {
      requestBody         : this.$route.query,
      boardNo             : this.$route.query.boardNo,
      boardUserId         : '',
      userNo              : localStorage.getItem('user_no'),
      boardTitle          : '',
      content             : '',
      contentFromChild    : '',
      filesFromChild      : [],
      tagsFromChild       : [],
      regDate             : '',
      tags                : '',
      uploadData          : []
    };
  },
  mounted() {
    if (this.boardNo !== undefined) { 
      this.fnGetView()      //      
    }
  },
  methods: {
    fnGetView() {
      if (this.boardNo !== undefined) { //    (boardNo )
        this.$axios.get(this.$boardUrl + '/all/getBoard/' + this.boardNo).then((res) => {
          this.boardTitle = res.data.board.boardTitle
          this.boardUserId = res.data.board.boardUserId
          this.contentFromChild = res.data.board.boardContent
          this.regDate = res.data.board.regDate
          this.tags = res.data.tags

          // TagInput    tagsFromChild 
          if(this.tags != null){
            for(const key in this.tags){
              if(Object.hasOwnProperty.call(this.tags, key)){
                this.tagsFromChild = this.tags[key].tagName;
                //console.log('tag-for : ' + this.tagsFromChild);
                this.$refs.tagInputRef.addTag(this.tagsFromChild);  //  tag  add 
              }
            }
          }
        }).catch((err) => {
          console.log(err)
        })
      }else{
        if (this.$store.state.account) {
          this.boardUserId = localStorage.getItem('user_id');
        }
      }
    },
    
    fnList() {
      this.$router.push({
        path: './detail',
        query: this.requestBody
      })
    },

    fnView(boardNo) {
      this.requestBody.boardNo = boardNo
      this.$router.push({
        name: 'BoardDetail',
        query: this.requestBody
      });
    },

    onEditorChange(content) {
      this.contentFromChild = content;
      //console.log('this.contentFromChild : ' + this.contentFromChild);
    },

    onImageAdded(file){
      console.log('file : ' + file);
      this.filesFromChild.push(file);
      //this.filesFromChild=file;
      this.filesFromChild.forEach(file =>{
        console.log('this.filesFromChild : ' + file);
        console.log('file_type1 : ' + typeof file);
      })
    },

    fnSave() {
      //console.log('fnSave()');
      const apiUrl = this.$boardUrl + '/admin/saveBoard'
      const tagsFromChild = this.$refs.tagInputRef.tags; //   tag data
      //    boardContent img     
      if(this.boardNo === undefined){ 
        let form = {}
        if(this.filesFromChild.length > 0) {    //      
          form = {
            "boardNo"       : this.boardNo,
            "userNo"        : this.userNo,
            "boardTitle"    : this.boardTitle,
            //"boardContent"  : this.contentFromChild,
            //"tags"          : tagsFromChild.join(', ') // 
            "tags"          : tagsFromChild //  
          };
        }else{
          form = {
            "boardNo"       : this.boardNo,
            "userNo"        : this.userNo,
            "boardTitle"    : this.boardTitle,
            "boardContent"  : this.contentFromChild,
            "tags"          : tagsFromChild //  
          };
        }
        
        console.log(' :', form);

        this.$axios.post(apiUrl, form, {
          headers : {
            'Content-Type' : 'application/json',
          }
        })
        .then((res) => {    //       
          this.boardNo = res.data;
          if(this.filesFromChild.length > 0) {    //    
            this.uploadData = new FormData();
            this.filesFromChild.forEach(file => {
              this.uploadData.append('files', file);
            });
            this.uploadData.append('boardNo', this.boardNo);

            this.$axios.post(this.$boardUrl+'/admin/uploadImg', this.uploadData, {
              headers : {
                'Content-Type': 'multipart/form-data'
              }
            }).then((res) => { //    
              if(this.contentFromChild) {
                // editor  html  content img 
                const imgSources = base64.imginHtmlChk(this.contentFromChild);    
                if(imgSources) { //   base64 ->   url  
                  this.boardContent = base64.updateBase64ToServerUrl(this.contentFromChild, imgSources, res.data);
                  let imgPathList = [];
                  imgPathList=res.data;     // board_img    
                  //console.log('this.boardContent : ' + this.boardContent);

                  const form = {
                    'boardContent' : this.boardContent,   // img-src  base64->url  content
                    'imgPathList'  : imgPathList,
                    'boardNo'      : this.boardNo
                  } 
  
                  const apiUrl = this.$boardUrl + '/admin/saveImg';
                  this.$axios.post(apiUrl, form, {
                    headers : {
                      'Content-Type' : 'application/json',
                    }
                  })
                  .then((res)=>{
                    this.fnView(res.data.board.boardNo)   //     
                  }).catch((err)=>{
                    if (err.message.indexOf('Network Error') > -1) {
                      alert('  .\n   .')
                    }else if(err.response && err.response.status === 500){
                      alert('error');
                    }else if(err.response && err.response.status === 403){
                        alert(' .  ');
                    }
                  })
                }
              }
            }).catch((err) => {
              if (err.message.indexOf('Network Error') > -1) {
                alert('  .\n   .')
              } else if(err.response && err.response.status === 500){
                alert('error');
              }
            });
          }else{
            
            this.fnView(res.data)   //     
          }
          alert(' .')
        }).catch((err) => {
          if (err.message.indexOf('Network Error') > -1) {
            alert('  .\n   .')
          }
        })
      }else{    //  
        const form = {
          "boardNo"       : this.boardNo,
          "boardTitle"    : this.boardTitle,
          "boardContent"  : this.contentFromChild,
          //"tags"          : tagsFromChild.join(', ') // 
          "tags"          : tagsFromChild //  
        };
        //console.log(' :', form);
        this.$axios.put(apiUrl, form,{
          headers : {
            'Content-Type' : 'application/json',
          }
        })
        .then((res) => {  
          alert(' .')
          //console.log('res.data : ' + JSON.stringify(res.data));
          this.fnView(res.data.boardNo)
        }).catch((err) => {
          if (err.message.indexOf('Network Error') > -1) {
            alert('  .\n   .')
          }
        })
      }
    },
  }
}
</script>

<style>
.board-container {
    max-width: 100%;
    max-height: 800px;
    height: 800px;
    margin: 0 auto;
    padding: 20px;
}

.board-title {
    background: transparent;
    display: block;
    padding: 10px;
    font-size: 2rem;
    width: 100%;
    resize: none;
    line-height: 1.5;
    outline: none;
    border: none;
    font-weight: bold;
}

.seperator {
    background: rgb(73, 80, 87);
    height: 4px;
    width: 4rem;
    border-radius: 1px;
}

.editor-container {
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    border: none;
    
}

.editor-page {
    width: 100%;
    max-width: 100%; /*    .    */
    margin-right: 200px;
    max-height: 1000px;
    height: 100%;
}

.board-btn{
    position: relative;
}

.btn-board-cancle{
    position: absolute;
    top: 100px;
    left: 0px;
}

.btn-board-save{
    position: absolute;
    top: 100px;
    left: 450px;
}


</style>